body,
html {
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  overflow-x: hidden; 
}





@font-face {
    font-family: "KyivType-Titling";
    src: url("../fonts/KyivTypeTitling-Bold2.otf") format("otf"),
        url("../fonts/KyivTypeTitling-Bold2.woff") format("woff");
}

@font-face {
    font-family: "KyivType-Serif";
    src: url("../fonts/KyivTypeSerif-Medium2.otf") format("otf"),
        url("../fonts/KyivTypeSerif-Medium2.woff") format("woff");
}


%heading_font {
    font-family: "KyivType-Titling", serif;
    color: #fedd43;
    text-transform: uppercase;
    font-size: 12rem;
    line-height: 12rem;
}

%subheading_font {
    font-family: "KyivType-Serif", serif;
    color: #fedd43;
    font-size: 2.6rem;
    line-height: 2.6rem;
}

%form_font {
    opacity: 1;
    font-family: "KyivType-Serif", serif;
    color: $dark_blue;
    font-size: 1.5rem;
    line-height: 1.5rem;
}



.grid_container {
    display: grid;
    grid-template-columns: 1fr;
    // grid-template-rows: repeat(6, 1fr);

}

// .grid_container>div {
//     height: 200px;
// }

//Opener section
.opener {
    background-color: $green;
    height: 200px;
}

.slider {
    background-color: $yellow;
}

.table {
    background-color: $dark_blue;
    height: 200px;
}

.tvset {
    background-color: $orange;
    height: 200px;
}
// Slider section
.slider {
    min-height: 80vh;
    height: 80vh;
    overflow: hidden;
}

.slider_wrapper {
    margin-top: 10vw;
  }
  
  .slider_wrapper h2 {
    text-align: center;
    font-family: "KyivType-Serif", serif;
    color: #1d2c77;
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .multiple-items div.slide_block {
    /* width:  150px; */
    height: 25vw;
    background-image: url(../img/paper.png);
    background-size: cover;
    font-family: "KyivType-Serif", serif;
    color: #fedd43;
    font-size: 1.5rem;
    font-weight: bold;
    padding: 30% 5% 5%;
    text-align: center;
  }
// Offer section
.offer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    min-height: 80vh;
    background-color: $orange;
    // background-image: url(../img/contact_form-08.png);
    // background-size: 100% auto;
    // background-repeat: repeat-y;
}
.offer_pic_background {
    background-image: url(../img/contact_form-08.png);
    background-size: 100% auto;
    background-repeat: repeat-y;
    grid-column: 1/4;
    grid-row: 1/2;
    z-index: 2;
}
.offer_pic_bottom {
    background-color: aqua;
    grid-row: 4/5;
    grid-column: 1/4;
    z-index: 7;
}

.ticket_card {
    background-color: transparent;
    border: none;
    padding: 20px;
    grid-row: 1/4;
 }
 .card_first.ticket_card {
    grid-column: 1/2;
    z-index: 3;
 }  
 .card_second.ticket_card {
    grid-column: 2/3;
    z-index: 3;
 }  
 .card_third.ticket_card {
    grid-column: 3/4;
    z-index: 3;
 }  
  .ticket_body {
    background-color: #fff;
    position: relative;
    @extend %form_font;
  }
  
  .ticket_body i.circle_lt,
  .ticket_body i.circle_rt,
  .ticket_body i.circle_lb,
  .ticket_body i.circle_rb {
    position: absolute;
    font-size: 10px;
    color: #0d195f;
  }
  .ticket_body i.circle_lt {
    top: 10px;
    left: 10px;
  }
  .ticket_body i.circle_rt {
    top: 10px;
    right: 10px;
  }
  .ticket_body i.circle_lb {
    bottom: 10px;
    left: 10px;
  }
  .ticket_body i.circle_rb {
    right: 10px;
    bottom: 10px;
  }
  .ticket_heading {
    text-align: center;
    @extend %form_font;
  }
  .ticket_price {
    text-align: center;
    @extend %subheading_font;
  }
  
  .ticket_list ul {
    list-style-type: "-";
  }
  

  img.half-width {
    width: 100%;
    height: auto;
}
// Question section

.accordion-panel {
    display: none;
}


.question {
    background-color: $pink;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    min-height: 80vh;
    height: auto;

}
.question >div {
    border: solid 1px #000;
}
.question_pic_left {
    grid-row: 1/2;
    grid-column: 1/6;
}
.question_message { 
    grid-row: 1/2;
    grid-column: 6/13;
}
.question_wrapper { 
    grid-row: 2/3;
    grid-column: 3/11;
}

.question_header {
    background-color: #0d195f;
    border: none;
    border-radius: 18px;
    padding: 0 20px;
    @extend %form_font;
    color: $yellow;
  }
// Contact form
.contact {
    // background-color: $pink;
    background-image: url(../img/contact_form-02-min.png);
    background-size: cover;
    min-height: 80vh;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(9, 1fr);
}

.contact_column {
    grid-column: 5/9;
    grid-row: 1/9;
}

//Form Fields
.wpcf7-form input::-webkit-input-placeholder {
    color: $dark_blue;
}   
  .wpcf7-form input::-moz-placeholder {
    color: $dark_blue;
}
  .wpcf7-form textarea::-webkit-input-placeholder {
    color: $dark_blue;
}
  .wpcf7-form textarea::-moz-placeholder {
    color: $dark_blue;
}

.wpcf7-form textarea{
    width: 100%;
    @extend %form_font;
}

.wpcf7-form input {
     width: 100%;
    @extend %form_font;
}
.wpcf7-form label {
    display: block;
}
.wpcf7-form input[type="submit"] {
    background-color: $orange;
    background-image: url(../img/contact_form-09-min-150x52.png);
    background-position: center;
    color: $dark_blue;
    border: none;
    padding: 7px 20px;
    border-radius: 20px;
    text-transform: uppercase;
    display: block;
    margin: auto;
    width: fit-content;

}
%background_prop {
    background-size: contain;
   background-repeat: no-repeat;
}
  
  .pic_left_1 {
    background-image: url(../img/leaf_left.png);
   grid-column: 1/4;
   grid-row: 1/4;
   @extend %background_prop;
   transform: translateX(-10vw);
    
  }
  
  .pic_right_1 {
    background-image: url(../img/leaf_right.png);
    grid-column: 10/13;
    grid-row: 1/4;
    @extend %background_prop;
    transform: translateX(55%);
  }
  
  .pic_left_2 {
    background-image: url(../img/leaf_left2.png);
    grid-column: 1/4;
    grid-row: 3/6;
    @extend %background_prop;
    transform: translateX(-10vw);
  }
  
  .pic_right_2 {
    background-image: url(../img/contact_form-07-min.png);
    @extend %background_prop;
    grid-column: 8/13;
    grid-row: 4/10;
    // z-index: 25;
  }
  
  .pic_center {
    background-image: url(../img/contact_form-03-min.png);
    @extend %background_prop;
    transform: translate(-50%, -40%);
    z-index: 0;
  }